@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

:root {
  --title-font: 'Josefin Sans', sans-serif;
  --light-accent-color: #00E9AA;
  --dark-accent-color: #84FFC5;
  --accent-color: var(--dark-accent-color);

  --light-bg: white;
  --dark-bg: rgb(14, 14, 15);
  --bg: var(--light-bg);
}

body {
  background: white;
  background: var(--bg);
}

h1 {
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--title-font);
  font-weight: 500;
}
header nav {
  height: 80px;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header nav ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header nav ul li {
  margin-left: 20px;
  padding: 15px 20px;
  cursor: pointer;
  transition: background 200ms ease;
}

header nav ul li:hover {
  background: var(--accent-color);
}

header nav #header-logo {
  width: 50px;
}

header .landing-content {
  display: flex;
  align-items: center;
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
}

header .landing-content img {
  width: 60%;
  margin-left: auto;
}

header .landing-content .landing-message {
  padding: 20px;
  padding-bottom: 100px;
}

header .landing-content .landing-message h1 {
  font-size: 2.6rem;
}

@media (max-width: 600px) {
  header .landing-content {
    display: block;
  }
  header .landing-content img {
    width: 100%;
  }
  header .landing-content .landing-message {
    padding: 20px;
  }
  header .landing-content .landing-message h1 {
    font-size: 2.6rem;
  }
}
